.skills {
  ul {
    margin: 0 0 20px;
    padding-left: 10px;
  }

  li {
    display: flex;
    align-items: center;

    &::before {
      position: relative;
      left: -10px;
      content: "";
      width: 5px;
      height: 5px;
      background-color: black;
      border-radius: 5px;
      transition: color 0.3s;
    }
  }

  li + li {
    margin-top: 5px;
  }

  li:hover {
    &::before {
      background-color: red;
      transition: 0.3s;
    }
  }
}

