@font-face {
  font-family: "Inika";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    local("Inika"),
    url("../fonts/Inika-Regular.woff2") format("woff2"),
    url("../fonts/Inika-Regular.woff") format("woff");
}

@font-face {
  font-family: "Inika";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src:
    local("Inika"),
    url("../fonts/Inika-Bold.woff2") format("woff2"),
    url("../fonts/Inika-Bold.woff") format("woff");
}

